#home {
   /* height: 100%;
   overflow: hidden; */
   min-width: 375px;
}


/* Nav Bar */
#nav-wrap ul, #nav-wrap li {
   margin: 0;
   padding: 0;
   border: none;
   outline: none;

}

#nav-wrap {
   display: flex;
   position: fixed;
   font: 12px 'opensans-bold', sans-serif;
   letter-spacing: 2.5px;
   background-color: rgba(0, 0, 0, 0.2);
   text-transform: uppercase;
   width: 100%;
   height: 36px;
   z-index: 10;
   margin: 0 auto;
}

.nav {
   display: flex;
   width: 100%;
   align-items: center;
   justify-content: center;
}

.nav > li {
   list-style: none;
}
.nav > li > a{
   padding: 10px 13px;
   color: white;
   text-decoration: none;
}

/* hide toggle button */
#nav-wrap > a.mobile-btn { display: none; }

ul#nav li a:active { background-color: transparent !important; }
ul#nav li.current a { color: #F06000; }

ul#nav li > a:hover{
   color: #F06000;
   font-size: 12px;
   transition: 0.3s;
}

