.twinkling, .stars, .stars1, .stars2, .stars3, .header-background, .front-header {
   position: absolute;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
   width: 100%;
   height: 100%;
   display: block;
}

.stars {
   background: black url(../Images/stars.png) repeat top center;
   z-index: 0;
}

.stars1, .stars2, .stars3 {
   background: url(../Images/stars.png) repeat top center;
   z-index: 0;
} 

.stars1 {
   animation: moving-stars 15 0s linear infinite;
}

.stars2 {
   transform: scaleY(-1);
   left: 200px !important;
}

.stars3 {
   transform: scaleY(-1);
   left: 400px !important;
}

.twinkling {
   background: transparent url(../Images/twinkling.png) repeat top center;
   animation: move-twinkling-back 200s linear infinite;
   z-index: 1;
}

@keyframes move-twinkling-back {
   from {background-position: 0 0;}
   to {background-position: -10000px 5000px;}
}

@keyframes moving-stars {
   from {background-position: 0 0;}
   to {background-position: 1000px -1000px;}
}



header {
   position: relative;
   height: 100vh;
   min-height: 500px;
   width: 100%;
   /* background-size: cover !important;
	-webkit-background-size: cover !important; */
   text-align: center;
   overflow: hidden;
   box-sizing: border-box;
}

.front-header {
   background: url(../Images/front-header.png) center;
   min-height: 500px !important;
   background-size: cover !important;
    -webkit-background-size: cover !important;
   text-align: center;
   overflow: hidden;
   z-index: 3;
}

.header-background {
   background: url(../Images/header-background.jpg) no-repeat top center;
   min-height: 500px !important;
   background-size: cover !important;
    -webkit-background-size: cover !important;
   text-align: center;
   overflow: hidden;
   z-index: 2;
   opacity: 0.4;
}

.banner {
   display: inline-block;
   position: absolute;
   top: 27%;
   left: 15%;
   right: 15%;
   background-color: rgb(0, 0, 0, 0.7);
   border-radius: 2em;
   z-index: 5;
   text-decoration: none !important;
   min-width: 200px;
}

.banner-text > a {
   text-decoration: none;
}

hr {
   border: 0;
   height: 1px;
   background-image: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.75), rgba(255, 255, 255, 0));
}

.responsive-headline {
   color: white;
   position: relative;
   top: 10px;
   font-size: clamp(26pt, 7vw, 40pt) !important;
}

.view-more {
   display: flex;
   cursor: pointer;
   border: 2px solid white;
   width: fit-content;
   margin: auto;
   height: clamp(25pt, 5vw, 36pt);
   font-size: clamp(14pt, 1.2vw, 36pt);
   color: white;
   padding: 0px 10px;
   margin-bottom: 10px;
   align-items: center;
   overflow: hidden;
}

.view-more:hover {
   background-color: teal;
   transform: scale(0.9);
   transition: 0.6s;
}

.view-more:hover span {
   color: black;
   animation:  move-down-2 0.35s linear forwards;
   -webkit-animation: move-down-2 0.35s linear forwards;
}

.view-more > span {
   position: relative;
}

.view-more i {
   padding-left: 10px;
}

@keyframes move-down-2 {
	0% {top: 0px}
    35% {top: 50px;  }
    50% {top: -70px;  right: 200px;}
    65% {top: -20px; }
    80% {top: -10px; }
    100% {top: 0px;  }
}

.scroll-icon{
   color: #aae8ff;
}

#home> p {
   z-index: 10;
   position: absolute;
   bottom: 20px;
   left: 49%;
}

p > a > i {
   color: rgb(255, 255, 255);
   transform: scale(1.4);
}

p > a > i:hover {
   color:#aae8ff;
}

h2 {
   color: aliceblue;
   font-size: clamp(20pt, 6vw, 30pt);
}

header .banner-text h1 {
   margin: 0 auto 12px auto;
   font-weight: 700;
}

h1 > .name {
   color: var(--elementColor);
}

h1> .dot {
   font-size: 26pt;
}

.scrolldown:hover {
   transform: scale(1.15);
   transition: 0.5s;
   box-shadow:0 0px 70px white inset, 0 0px 10px white;

}
